import { createApp } from "vue"
import { createStore } from "vuex"
import { createRouter, createWebHistory } from "vue-router"

import App from "./App.vue"
import PersonalCabinet from "./components/cabinet/PersonalCabinet.vue"
import CreateTrain from "./components/cabinet/CreateTrain.vue"
import AddTrain from "./components/cabinet/AddTrain.vue"
import StatisticsOfTrain from "./components/cabinet/StatisticsOfTrain.vue"
import ButtonToCabinet from "./components/ui/ButtonToCabinet.vue"
import YourTrainings from "./components/trainings/YourTrainings.vue"

import TrainingContetnt from "./components/trainings/TrainingContent.vue"
import MainEnter from "./components/form/MainEnter.vue"

import UserRegistration from "./components/form/UserRegistration.vue"
const router = createRouter({
	history: createWebHistory(),
	routes: [
		{
			path: "/enter",
			component: MainEnter,
		},
		{
			path: "/registration",
			component: UserRegistration,
		},
		{
			path: "/",
			redirect: "/cabinet",
			component: PersonalCabinet,
		},
		{
			path: "/cabinet",
			component: PersonalCabinet,
		},
		{
			path: "/cabinet/createtrain",
			component: CreateTrain,
		},
		{
			path: "/cabinet/addtrain",
			component: AddTrain,
		},
		{
			path: "/cabinet/statistics",
			component: StatisticsOfTrain,
		},
		{
			path: "/cabinet/trainings",
			component: YourTrainings,
		},
		{
			path: "/cabinet/trainings/:numberId",
			component: TrainingContetnt,
		},
	],
})
const store = createStore({
	state() {
		return {
			persResults: [],
			finalResult: [],
		}
	},

	mutations: {
		createPost(state, payload) {
			state.persResults.unshift(payload)
			console.log(state.persResults)
		},

		addFinalResult(state) {
			if (state.persResults.length === 0) {
				return
			} else {
				state.finalResult.push(state.persResults)

				state.persResults = []
			}

			console.log(state.finalResult)
			console.log(state.persResults)
		},
	},

	actions: {},

	getters: {
		createPost(state) {
			return state.persResults
		},

		addExersice(state) {
			return state.persResults
		},

		checkData(state) {
			if (state.persResults.length === 0) {
				return true
			}
		},
		outputDataOfTrain(state) {
			return state.persResults
		},
		checkDataTrainings(state) {
			return state.finalResult
		},
	},
})

const app = createApp(App)
app.component("button-cabinet", ButtonToCabinet)
app.use(store)
app.use(router)

app.mount("#app")
