<template>
	<h3>Упражнение 1</h3>
	<form @submit.prevent="submitInputsData" id="dataExercise">
		<div class="input">
			<input
				type="text"
				placeholder="Введите название упражнения"
				v-model.trim="enterName"
			/>
		</div>
		<div class="input">
			<input
				type="number"
				placeholder="Введите количество подходов"
				v-model="amountOfExercise"
			/>
		</div>
		<div class="input">
			<input
				type="number"
				placeholder="Введите количество повторений"
				v-model="amouOfRepeat"
			/>
		</div>
		<div class="input">
			<input
				type="text"
				placeholder="Введите вес"
				v-model.trim="weight"
			/>
			<p class="example">формат ввода: 40:50:60:70</p>
		</div>
		<div v-if="invalid" class="invalid">
			<p>Вы заполнили не все поля</p>
		</div>

		<!-- <div v-if="yes">Данные добавлены</div> -->
		<!-- <div v-if="prompt">
			Данные добавлены <br />
			<button @click="closePrompt">ok</button>
		</div> -->
	</form>
	<div class="buttons__wrap">
		<button form="dataExercise" type="submit" class="button-add">
			Добавить
		</button>
		<div class="button-return">
			<router-link to="/cabinet/createtrain">Назад</router-link>
		</div>
	</div>
	<layer-train v-if="prompt">
		<prompt-train @close-prompt="closePrompt"></prompt-train>
	</layer-train>
</template>

<script>
// import { mapMutations } from "vuex"
import LayerTrain from "../ui/layerForTrain.vue"
import PromptTrain from "./PromptAddTrain.vue"

export default {
	components: {
		LayerTrain,
		PromptTrain,
	},
	data() {
		return {
			enterName: "",
			amountOfExercise: "",
			amouOfRepeat: "",
			weight: [],
			invalid: false,
			prompt: false,
			// arr: [
			// 	"a",
			// 	"b",
			// 	"c",
			// 	"d",
			// 	"e",
			// 	"f",
			// 	"g",
			// 	"h",
			// 	"i",
			// 	"j",
			// 	"k",
			// 	"l",
			// 	"m",
			// 	"n",
			// 	"o",
			// 	"p",
			// 	"q",
			// 	"r",
			// 	"s",
			// 	"t",
			// 	"u",
			// 	"v",
			// 	"w",
			// 	"x",
			// 	"y",
			// 	"z",
			// ],
		}
	},

	methods: {
		submitInputsData() {
			if (
				this.enterName === "" ||
				this.amountOfExercise === "" ||
				this.amouOfRepeat === "" ||
				this.weight.length === 0 ||
				this.weight === ""
			) {
				this.invalid = true
			} else {
				this.$store.commit("createPost", {
					// id: this.createRandomId(this.arr),
					exsName: this.enterName,
					amountOfExercise: this.amountOfExercise,
					amouOfRepeat: this.amouOfRepeat,
					weight: this.weight.split(":"),
				})
				this.enterName = this.amountOfExercise = this.amouOfRepeat = ""
				this.weight = []
				this.invalid = false
				this.changeStatusPrompt()
			}
		},
		changeStatusPrompt() {
			if (this.prompt === false) {
				this.prompt = true
			}
		},
		closePrompt() {
			this.prompt = false
		},
		// createRandomId(arr) {
		// 	return (
		// 		arr[Math.floor(Math.random() * arr.length)] +
		// 		Math.floor(Math.random() * 100) +
		// 		arr[Math.floor(Math.random() * arr.length)]
		// 	)
		// },
	},
}
</script>

<style scoped>
h3 {
	text-align: center;
	margin-bottom: 30px;
	padding: 5px;
}

input {
	padding: 7px 5px;
}

.example {
	margin-top: 5px;
	display: inline-block;
	text-align: right;
	background: white;
	padding: 7px 5px;
}
button {
	text-align: center;
	padding: 10px 30px;
	background: white;
	border: 1px solid white;
	border-radius: 9px;
}
.button-add {
	text-align: center;
	padding: 10px 30px;
	background: white;
	border: 1px solid white;
	border-radius: 9px;
}

.button-return {
	background: white;
	border: 1px solid white;
	border-radius: 9px;
}

.button-return a {
	padding: 10px 30px;
	width: auto;
	display: flex;
}

.input {
	margin-bottom: 20px;
}

.buttons__wrap {
	display: flex;
	justify-content: space-around;
}

.invalid {
	text-align: center;
	padding: 10px 0px;
	color: red;
	font-size: 20px;
}
</style>
