<template>
	<h3>Статистика упражнения</h3>
	<div class="item">
		<p class="item__name">
			Название упражение:
			<span>{{ outputDataOfName }}</span>
		</p>
	</div>
	<div class="item">
		<p class="item__name">
			Количестов подходов: <span>{{ amountOfExercise }}</span>
		</p>
	</div>
	<div class="item">
		<p class="item__name">
			Количестов повторений: <span>{{ amouOfRepeat }}</span>
		</p>
	</div>
	<div class="item">
		<p class="item__name">Вес:</p>
		<p class="item__type" v-for="(item, index) in weightData" :key="item">
			{{ (index = index + 1) }}-подход:
			<span>{{ item }}</span>
		</p>
	</div>
	<div class="button">
		<router-link to="/cabinet/createtrain"> В меню тренировки </router-link>
	</div>
</template>
<script>
export default {
	computed: {
		outputDataOfName() {
			let data = this.$store.getters["outputDataOfTrain"]
			return data.filter((item, index) => index === 0)[0].exsName
		},
		amountOfExercise() {
			let data = this.$store.getters["outputDataOfTrain"]
			return data.filter((item, index) => index === 0)[0].amountOfExercise
		},
		amouOfRepeat() {
			let data = this.$store.getters["outputDataOfTrain"]
			return data.filter((item, index) => index === 0)[0].amouOfRepeat
		},
		weightData() {
			let data = this.$store.getters["outputDataOfTrain"]

			return data.filter((item, index) => index === 0)[0].weight
		},
	},
}
</script>
<style scoped>
h3 {
	padding: 5px;
	text-align: center;
	margin-bottom: 30px;
}

p {
	font-weight: 600;
}

span {
	font-weight: 400;
	color: red;
}

.item {
	margin-bottom: 20px;
}

.item__type {
	font-weight: 400;
}

.button {
	text-align: center;
	padding: 10px;
	background: white;
	max-width: 155px;
	margin: 0 auto;
	border: 1px solid white;
	border-radius: 9px;
}
</style>
