<template>
	<ul class="list">
		<li v-for="item in addExersice" :key="item">{{ item.exsName }}</li>
	</ul>
</template>

<script>
export default {
	data() {
		return {
			visible: true,
		}
	},

	computed: {
		addExersice() {
			let out = this.$store.getters["addExersice"]
			return out.filter((item) => item.exsName)
		},
	},
}
</script>

<style scoped>
li {
	padding: 5px 10px;
	text-align: left;
	background: white;
	margin-bottom: 7px;
}

.list {
	margin-bottom: 40px;
}
</style>
