<template>
	<h3>Тренировка 1</h3>
	<p class="info" v-if="checkData">У вас пока нет ни одного упражнения</p>

	<create-training> </create-training>

	<div class="button-wrap">
		<button>
			<router-link to="/cabinet/addtrain">
				<img src="@/assets/img/add.svg" alt="" />
			</router-link>
		</button>
	</div>
	<p class="create">Создать упражнение</p>
	<div class="buttons-wrap">
		<router-link to="/cabinet">
			<button-cabinet></button-cabinet>
		</router-link>
		<button class="finish-btn" @click="openModal">
			Закончить c тренировками
		</button>
	</div>
	<finish-training
		v-if="status"
		@active-non="activeNon"
		@active-confirm="activeConfirm"
	></finish-training>
</template>

<script>
import CreateTraining from "./CreateItemOfTraining.vue"
import FinishTraining from "../ui/FinishTraining.vue"
export default {
	components: {
		CreateTraining,
		FinishTraining,
	},

	data() {
		return {
			status: false,
		}
	},

	methods: {
		openModal() {
			this.status = true
		},
		activeConfirm() {
			this.$store.commit("addFinalResult")
			this.status = false
		},
		activeNon() {
			this.status = false
		},
	},

	computed: {
		checkData() {
			return this.$store.getters["checkData"]
		},
	},
}
</script>

