	text-align: center;
	background: white;
	padding: 30px 0;
	margin-bottom: 40px;
}

.button-wrap {
	display: flex;
	justify-content: center;
	margin-bottom: 10px;
}

.create {
	text-align: center;
	margin-bottom: 30px;
}

.buttons-wrap {
	display: flex;
	justify-content: space-evenly;
}

.finish-btn {
	max-width: 140px;
	width: 100%;
	padding: 9px 6px;
	background: black;
	font-size: 16px;
